
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#app audio {
  width: 600px;
}
#app .control {
  margin: 1em 0;
}
#app .control label {
  margin: 0 1em;
}
#app .control input[type=number] {
  font-size: 1em;
  text-align: center;
}
